<template>
  <div class="chat-home-root">
    <!-- 显示一张主播图片加拨打按钮 -->
    <div class="a-host">
      <van-overlay :show="showLoadingOverlay" class="loading-overlay">
        <van-loading size="24px" color="#1989fa" vertical
          >Loading...</van-loading
        >
      </van-overlay>
      <!--  -->
      <div
        v-if="curHost.avatar"
        class="avatar"
        :style="{ backgroundImage: `url(${curHost.avatar})` }"
      ></div>
      <div class="call-overlay">
        <img
          class="to-call"
          :src="require('@/assets/icon/video-button.png')"
          @click="randomCall"
        />
        <p class="call-text">{{ strings.chatWithMe }}</p>
      </div>
    </div>
    <!--  -->
    <Connect
      ref="link"
      :uid="curHost.uid"
      :poster="curHost.avatar"
      @close="closeConnect"
      :key="Date.now()"
      v-if="connectShow"
    />
    <RechargeDialog
      :open="showRechargeTips"
      @confirm="rechargeNow"
      @cancel="showRechargeTips = false"
    ></RechargeDialog>
  </div>
</template>

<script>
import Vue from "vue";
import { Overlay, Loading } from "vant";
// import ChatCard from "../components/ChatCard";
import * as Server from "@/server";
// import * as Storage from "../storage";
import { getStrings } from "../strings";
import Connect from "../components/Connect";
import RechargeDialog from "../components/dialog/RechargeDialog.vue";
import firebase from "firebase/app";
import "firebase/auth";
import crypto from "crypto";
// import router from '../router';

Vue.use(Overlay).use(Loading);
// .use(NavBar);
export default {
  components: {
    // ChatCard,
    Connect,
    RechargeDialog
  },
  data() {
    return {
      strings: getStrings(),
      page: 1,
      chats: [],
      refreshing: false,
      loading: false,
      finished: true,
      profile: {},
      lastDBBanner: 0,
      connectShow: false,
      curHost: {},
      alreadyLogin: false,
      showLoadingOverlay: true,
      fromLivetube: null,
      ruid: Math.random()
        .toString()
        .substr(2)
    };
  },
  created: function() {
    console.log("Home Created");
    let profile = JSON.parse(localStorage.getItem("profile") || "{}");
    let token = localStorage.getItem("token");
    if (!profile.uid || !token) this.startMuteLogin();
    else this.loginSuccess();
    this.handleFrom();
  },
  methods: {
    handleFrom() {
      let from = this.getUrlparam("from");
      let uid = this.getUrlparam("uid");
      console.log("HandleFrom: ", from, uid);
      if (from === "livetube" && uid) {
        this.curHost.uid = uid;
        this.fromLivetube = uid;
      }
    },
    async tracking() {
      let profile = JSON.parse(localStorage.getItem("profile") || "{}");
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      if (!profile.uid || !trackInfo.source) {
        console.log("no trackinfo => ", profile, trackInfo);
        return;
      }
      let { status, data } = await Server.TrackSourceFrom(
        profile.uid,
        trackInfo.source,
        trackInfo.refby
      );
      console.log("Tracking: ", status, " => ", data);
    },
    async anonymousLogin(user) {
      const requestTime = Date.now();
      const deviceType = 4;
      const regionCode = firebase.auth().languageCode || "cn";
      // let appId = +(
      //   JSON.parse(localStorage.getItem("trackInfo") || "{}").appId || 305
      // );
      let appId = 306;
      const { photoURL: avatar, email, uid, displayName: nickName } = user;
      let loginId = uid;
      // 支持各种类型的接入=0，代表匿名登陆，=1，代表邮箱，=2代表手机，=3代表google，=4代表 facebook
      const thirdType = 6;
      const deviceCode = uid;
      const md5 = crypto.createHash("md5");
      const substr = "fjsihWdgr;_#78JI9&)!kjGHRERGRFeu342";
      const sign = md5.update(loginId + requestTime + substr).digest("hex");
      let params = {
        appId,
        loginId,
        thirdType,
        deviceCode,
        deviceType,
        requestTime,
        sign,
        regionCode,
        nickName,
        avatar,
        email
      };
      const { status, data } = await Server.Login(params);
      if (status === 0) {
        const { token, user: vUser } = data;
        localStorage.setItem("token", token);
        localStorage.setItem("profile", JSON.stringify(vUser));
      }
    },
    async loginSuccess(param) {
      if (!param) param = new Date().toString();
      console.log("AlreadyLogin ：", param);
      this.alreadyLogin = true;
    },
    startMuteLogin() {
      firebase
        .auth()
        .signInAnonymously()
        .then(async res => {
          let user = res.user;
          console.log("signInAnonymously", user);
          await this.anonymousLogin(user);
          await this.loginSuccess();
          this.tracking();
        })
        .catch(error => {
          this.showLoginOverlay = false;
          this.$toast(error.message);
        });
    },
    closeConnect() {
      this.connectShow = false;
    },
    showRecharge() {
      if (Date.now() - this.lastDBBanner < 1000) {
        this.$router.push("/recharge?showInfo");
        console.log("Router push recharge");
      }
      this.lastDBBanner = Date.now();
    },
    moreToPersonal() {
      this.$router.push("/recharge");
    },
    getUrlparam(key, url) {
      if (!url) url = window.location.href;
      let index = url.indexOf("?");
      if (index < 0) return null;
      url = url.substr(index + 1);
      let arr = url.split("&");
      let result = null;
      arr.forEach(value => {
        let cur = value.split("=");
        if (cur[0] === key) {
          result = cur[1];
          return;
        }
      });
      return result;
    },
    async countAccess() {
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      let uid = JSON.parse(localStorage.getItem("profile") || "{}").uid;
      if (!trackInfo.source) trackInfo.source = trackInfo.appID || 305;
      if (!trackInfo.refby) trackInfo.refby = trackInfo.appID || 305;
      let { status, msg } = await Server.countAccess(
        uid,
        trackInfo.source,
        trackInfo.refby
      );
      console.log("CountAccess: ", status, msg);
    },
    async loadData() {
      const { data, status } = await Server.HomeList(this.ruid, this.page);
      this.refreshing = false;
      this.loading = false;
      if (status === 0) {
        const { records, total } = data;
        if (this.page === 1) {
          this.chats = records || [];
        } else {
          this.chats = this.chats.concat(records || []);
        }
        if (this.chats.length >= total) {
          this.finished = true;
        } else {
          this.finished = false;
        }
      }
      let rd = ~~(Math.random() * this.chats.length);
      this.curHost = this.chats[rd];
      let url = window.location.href;
      if (this.fromLivetube) {
        this.curHost.uid = this.fromLivetube;
      } else if (/inTest/gi.test(url)) {
        this.curHost.uid = 3301603086703212; // 全部拨打给自己，测试中
      }
      // this.countAccess();
    },
    onLoad() {
      this.page = this.page + 1;
      this.loadData();
    },
    onRefresh() {
      this.page = 1;
      this.loadData();
    },
    goChatDetail(item) {
      console.log("chat detail => ", item);
      // this.$router.push(`chat-detail/${item.uid}`);
      // this.$router.push(`chat-detail/3311605876848841`);
    },
    async queryBalance() {
      let { status, data } = await Server.QueryBalance(this.profile.uid);
      console.log("QueryBalance => ", status, data);
      return data;
    },
    rechargeNow() {
      this.$router.push("/recharge?showInfo");
    },
    randomCall() {
      if (!this.alreadyLogin) {
        this.$toast("Please waiting a minute...");
      } else {
        this.connectShow = true;
        this.showLoadingOverlay = false;
      }
    }
  },
  async mounted() {
    await this.loadData(); // 获取在线主播列表，从中随机抽取一个主播，作为封面显示
  },
  watch: {
    $route(to, from) {
      console.log("Router change: ", to, " => ", from);
      if (/\/login/gi.test(from.path)) {
        console.log("From Login, Refresh List.", new Date().toString());
        this.chats = [];
        this.page = 1;
        this.loadData();
        this.$router.replace("/home");
      }
    }
  }
};
</script>

<style lang="less">
.chat-home-root {
  .a-host {
    width: 100vw;
    height: 100vh;
    position: relative;

    & > * {
      width: 100%;
      height: 100%;
      position: absolute;
    }
    .loading-overlay {
      z-index: 1;
      width: 100vw;
      height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;
      background: transparent;
    }
    .avatar {
      z-index: 9;
      // object-fit: cover;
      // width: 100%;
      // height: 100vh;
      // object-position: center;
      height: 100vh;
      background-repeat: no-repeat;
      background-size: auto;
      background-position: center;
    }
    .call-overlay {
      z-index: 99;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
      background: #26262636;

      .to-call {
        width: 70px;
      }

      .call-text {
        font-size: 18px;
        font-weight: 500;
        color: #ffe462;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
          Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
        margin: 10px 0 50px 0;
        text-shadow: 2px 2px #26262636;
      }
    }
  }
}
.more-to-personal {
  text-align: right;
  font-size: 14px;
  line-height: 20px;
  color: #262626;
}
.chat-home-root {
  // padding: 0 10px;
  // padding-top: 10px;
  background-clip: #fff;
}
.card-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}
</style>
